.input {
  box-sizing: border-box;
  padding: 8px 20px;
  font-size: 14px;
  line-height: 20px;
  border: 1px solid #d1d1d1;
  border-radius: 5px;
  outline: none;
  background-color: transparent;
  font-weight: 600;
}

.label {
  position: relative;
  box-sizing: border-box;
  min-width: 300px;
}
